	friendlyPlay (data) {
		if(data.hasOwnProperty("accept")){
			socket.json.send({'method': 'friendlyInvite', accept: data.accept, friend_id: data.friend_id});
		}else{
			socket.json.send({'method': 'friendlyPlay', 'lat': data.lat, 'long': data.long, 'available': data.available});
		}
	}

	friendlyInvite (user_id, value, i){
		socket.json.send({'method': 'friendlyInvite', friend_id: user_id});
	}

	selectPerson () {
		socket.json.send({'method': 'select', 'select': 'person', 'person': this.state.pers});
	}

	selectStock (i) {
		socket.json.send({'method': 'select', 'select': 'stock', 'stock': i});
	}

	stockItems (i){
		if (i === 0) {
			var l = [1,2,3,4];
			return l.map((number)=>
				<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
					<img src={number === 1 ? noob_ny : number === 2 ? whisper_ny : number === 3 ? atlas_ny : hr_ny} style={{height:"100%"}}/>
				</div>)
				;
		}else{
			return null;
		}
	}

	convertMiliseconds(miliseconds) {
		var days, hours, minutes, seconds, total_hours, total_minutes, total_seconds;

		total_seconds = parseInt(Math.floor(miliseconds / 1000));
		total_minutes = parseInt(Math.floor(total_seconds / 60));
		total_hours = parseInt(Math.floor(total_minutes / 60));
		days = parseInt(Math.floor(total_hours / 24));

		seconds = parseInt(total_seconds % 60);
		minutes = parseInt(total_minutes % 60);
		hours = parseInt(total_hours % 24);

		return days+'д. '+hours+'ч. '+minutes+'мин.';
	};

	stockButton (value, i){
		var date = Date.now();
		if(value.stockBought){
			if(value.stockChanged){
				return <Button onClick={()=>{this.selectStock(i);}} mode="secondary" size="xl" style={{ marginTop:"36px", marginBottom:"24px", height: "55px" }}>Выбран</Button>;
			}else{
				return <Button onClick={()=>{this.selectStock(i);}} mode="secondary" size="xl" style={{ marginTop:"36px", marginBottom:"24px", height: "55px" }}>Выбрать</Button>;
			}
		}else{
			if((date >= value.start) && (date < value.end)){
				return <Button onClick={()=> {this.buyStock(i);}} mode="commerce" size="xl" style={{marginTop:"36px", marginBottom:"24px", height: "55px"}}
						after={<Counter>{value.price}</Counter>}>Купить всё</Button>;
			}else{
				return <Button disabled mode="commerce" size="xl" style={{marginTop:"36px", marginBottom:"24px", height: "55px"}}
						after={<Counter>{value.price}</Counter>}>Купить всё</Button>;
			}
		}
	}

	click () {
		socket.json.send({'method': 'click', 'game_id': this.state.game.game_id});
	}

	exitGame () {
		socket.json.send({'method': 'exitGame', 'game_id': this.state.game.game_id});
	}

	exitGame (id) {
		socket.json.send({'method': 'exitGame', 'game_id': id});
	}

	reconnect () {
		try{socket.disconnect();}catch (e) {}
		this.setState({popout: <ScreenSpinner /> });
		this.login();
	}

	error (msg) {
		this.setState({ error : {action: msg.action, text: msg.text, end: msg.end }, snackbar: null });
		try{socket.disconnect();}catch (e) {}
		if(msg.end>0){
			var time = Date.now();
			if(msg.end<=time){
				var error = this.state.error;
				error.text = 'Приложение скоро включится';
				this.setState({error: error});
			}else{
				var interval = null;
				interval = setInterval(()=>{
					var end = msg.end-Date.now();
					if(end<=0){
						var error = this.state.error;
						error.text = 'Приложение скоро включится';
						this.setState({error: error});
						clearInterval(interval);
					}else{
						var error = this.state.error;
						error.text = 'Приложение включится через '+this.convertMiliseconds(end);
						this.setState({error: error});
					}
				},500);
			}
		}
		this.goToPage('error');
	}

	noty (text, image, style) {
		this.setState({ snackbar: null, activeModal: null, modalHistory: [] });
		setTimeout(()=>{
			this.setState({ snackbar:
					<Snackbar
						layout="vertical"
						onClose={() => this.setState({ snackbar: null })}
						before={<Avatar size={24} style={style}>{image}</Avatar>}
					>
						{text}
					</Snackbar>
			});
		},100);
	}

	noty (text, image, style, after) {
		this.setState({ snackbar: null, activeModal: null, modalHistory: [] });
		setTimeout(()=>{
			this.setState({ snackbar:
					<Snackbar
						layout="vertical"
						onClose={() => this.setState({ snackbar: null })}
						before={<Avatar size={24} style={style}>{image}</Avatar>}
						after={after}
					>
						{text}
					</Snackbar>
			});
		},100);
	}

	noty (text, image, style, action, onActionClick) {
		this.setState({ snackbar: null, activeModal: null, modalHistory: [] });
		if(action === null && onActionClick === null){
			this.noty(text,image,style);
		}else{
			setTimeout(()=>{
				this.setState({ snackbar:
						<Snackbar
							layout="vertical"
							onClose={() => this.setState({ snackbar: null })}
							before={<Avatar size={24} style={style}>{image}</Avatar>}
							action={action}
							onActionClick={onActionClick}
						>
							{text}
						</Snackbar>
				});
			},100);
		}
	}

