	achievement () {
		this.getShop();
		if (this.state.achievement && (this.state.achievement+'').includes('object')) {
			this.setActiveModal(MODAL_PAGE_ACHIEVEMENT);
		}else{
			this.noty('Новых заданий пока что нет', <Icon16Recent fill="#fff" width={14} height={14} />, blueBackground);
		}
	}

	et_clicks_onChange (e) {
		var { value } = e.currentTarget;
		this.setState({ et_clicks: value});
		var status = value  > 0 ? (value.toString().includes('.') ? 'error' : this.state.user.hasOwnProperty('bp') ? value > 500000 ? 'error' : 'valid' :value > 25000 ? 'error' : 'valid') : 'error';
		this.setState({ btn_clicks: status === 'valid' ? <Button onClick={()=>{ this.transfer(); }} size="xl" mode="commerce">Перевести</Button> : <Button disabled size="xl" mode="commerce">Перевести</Button> });
	}

	transfer () {
		socket.json.send({method: 'tr', user_id: this.state.tr_id, clicks: this.state.et_clicks});
	}

